    text-decoration: underline;
  }
  /* lotto */
  .lotto_result{
    color:pink;
    font-size: 30px;
  }
  .my_lotto{
    color:plum;
    font-size: 20px;
  }

  /* wave */
  .box {
    width: 100%;
    height: 300px;
    border-radius: 5px;
    box-shadow: 0 2px 30px rgba(black, .2);
    background: lighten(#f0f4c3, 10%);
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    z-index: -1;
  }
  
  .wave {
    opacity: .4;
    position: absolute;
    top: 3%;
    left: 50%;
    background: rgb(0, 255, 115);
    width: 500px;
    height: 500px;
    margin-left: -250px;
    margin-top: -250px;
    -webkit-transform-origin: 50% 48%;
            transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 3000ms infinite linear;
            animation: drift 3000ms infinite linear;
  }
  
  .wave.-three {
    -webkit-animation: drift 5000ms infinite linear;
            animation: drift 5000ms infinite linear;
    background: rgb(0, 110, 255);
  }
  
  .wave.-two {
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    opacity: .1;
    background: rgb(255, 0, 179);
  }
  
  .box:after {
    content: '';
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(#e8a, 1), rgba(#def, 0) 80%, rgba(white, .5));
    z-index: 11;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  
  .title {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
    line-height: 300px;
    text-align: center;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    color: white;
    text-transform: uppercase;
    font-family: 'Playfair Display', serif;
    letter-spacing: .4em;
    font-size: 24px;
    text-shadow: 0 1px 0 rgba(black, .1);
    text-indent: .3em;
  }
  .navbar{
    position: absolute;
  }
  .nav_menu{
    display: flex;
    list-style: none;
    padding-left: 0;
  }
  .nav_menu li{
    padding: 8px 12px;
  }
  .nav_icon{
    display: flex;
    list-style: none;
    padding-left: 0;
  }
  .nav_icon li{
    padding: 8px 12px;
  }
  .nav_toogleBtn{
    display: none;
    position:absolute;
    left: 2%;
    top: 2%;
    font-size: 25px;
  }

  @-webkit-keyframes drift {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  @keyframes drift {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  /* 반응형 */
  @media screen and (max-width:700px) {
    .navbar{
      left: 2%;
      top: 2%;
      flex-direction: column;
      align-items: flex-start;
    }
    .navbar div{
      margin-top: 50px;
    }
    .nav_menu{
      display: none;
      flex-direction: column;
      align-items: center;
      width: 100%;
    }
    .hide_nav_menu{
      flex-direction: column;
      align-items: center;
      width: 50%;
      height: 400px;
      margin-top: 20px;
      font-size: 17px;
    }
    .hide_nav_menu li{
      margin-left: 10px;
      margin-top: 15px;
      margin-bottom: 15px;
    }
    .hide_nav_menu li a {
      margin-left: 5px;
    }
    .nav_icon{
      justify-content: center;
      margin-top: 10px;
    }
    .nav_toogleBtn{
      display: block;
    }
  }
